import React from 'react'
import { Link } from 'gatsby'
import { parse, stringify } from 'query-string'

const TestqueryParams = params => {
  const { myQueryParam } = parse((params.location || {}).search)

  const p = stringify({ myQueryParam })
  const q = p
  return (
    <div>
      <h4>Hello from TestqueryParams</h4>
      <Link to={parse(`https://www.google.com?${params.location.search}`)}>
        My Link
      </Link>
    </div>
  )
}
export default TestqueryParams
